import React, { useState, useEffect } from "react";
import moment from "moment";
import "antd/dist/antd.css";
import "moment/locale/vi";
import {
  Form,
  Select,
  Button,
  Table,
  Popover,
  Divider,
  Typography
} from "antd";
import { Link } from "react-router-dom";
import Axios from "axios";

const Tabular = ({ id, token }) => {
  const [toDelete, setDelete] = useState(true);
  const [deleteRows, setDelRows] = useState([]);
  const [configTime, setCfgTime] = useState(null);
  const [keepRows, setKeepRows] = useState([]);
  const [data, setData] = useState([]);
  const [form] = Form.useForm();

  useEffect(() => {
    Axios.get(
      `https://graph.facebook.com/v6.0/${id}/comments?access_token=${token}`
    ).then(response => {
      setData(response.data.data);
    });
  },);

  const initialState = id => {
    setData(
      data.filter(item => {
        return item.id !== id;
      })
    );
  };

  const handleDelete = ([...ids]) => {

    for (let i = 0; i < ids.length; i++) {
      Axios.delete(
        `https://graph.facebook.com/v6.0/${ids[i].id}?access_token=${token}`
      )
        .then(response => {
          initialState(ids[i].id);
          alert("Xóa thành công");
        })
        .catch(error => {
          console.log("Xóa thất bại");
          console.log(error);
        });
    }
  };

  const handleKeepData = () => {
    setKeepRows(deleteRows);
    setDelRows([]);
    setDelete(true);
  };

  const rowSelection = {
    onChange: (selectedRowKeys, selectedRows) => {
      console.log(
        `selectedRowKeys: ${selectedRowKeys}`,
        "selectedRows: ",
        selectedRows,
        "Number of Selected Rows: ",
        selectedRows.length
      );
      if (selectedRows.length > 0) {
        setDelete(false);
        setDelRows(selectedRows);
        // setKeepRows(selectedRows);
      } else {
        setDelete(true);
      }
    },
    getCheckboxProps: record => ({
      name: record.name
    })
  };

  const autoStart = () => {
    setDelete(true);
    console.log("Chuẩn bị chạy tự động...");
    setInterval(() => {
      console.log("Bắt đầu");
      Axios.get(
        `https://graph.facebook.com/v6.0/${id}/comments?access_token=${token}`
      ).then(response => {
        setData(response.data.data);
        if (response.data.data.length === keepRows.length) {
          console.log("Không có gì để xóa");
        } else {
          var deleteData = [];
          for (let i = 0; i < data.length; i++) {
            if (
              !keepRows.includes(keepRows.find(item => item.id === data[i].id))
            ) {
              deleteData.push(data[i]);
            }
          }
          deleteData.forEach(item => {
            Axios.delete(
              `https://graph.facebook.com/v6.0/${item.id}?access_token=${token}`
            )
              .then(res => {
                console.log("Xóa thành công");
                setData(response.data.data);
              })
              .catch(error => {
                console.log("Xóa thất bại");
              });
          });
        }
      });
    }, configTime);
  };

  const cancelAuto = () => {
    alert("Hủy Auto thành công");
    window.location.reload();
  };

  const handleConfigChange = event => {
    // setCfgTime(event.target.value);
    // console.log(event);
    setCfgTime(event);
    console.log(event);
    console.log(configTime);
  };

  const content = (
    <Form form={form}>
      <Form.Item label="Thời gian">
        <Select onChange={handleConfigChange} style={{ width: 100 }}>
          <Select.Option value="5000">5 giây</Select.Option>
          <Select.Option selected value="10000">
            10 giây
          </Select.Option>
          <Select.Option value="30000">30 giây</Select.Option>
          <Select.Option value="60000">1 phút</Select.Option>
          <Select.Option value="300000">5 phút</Select.Option>
        </Select>
      </Form.Item>
      <Divider />
      <Button
        type="danger"
        size="middle"
        style={{ marginRight: 10 }}
        onClick={() => autoStart(id)}
      >
        Bắt đầu
      </Button>
      <Button type="ghost" size="middle" onClick={() => cancelAuto(autoStart)}>
        Hủy
      </Button>
    </Form>
  );
  const text = <h2>Cài đặt tự động</h2>;

  const columns = [
    {
      title: "Mã bình luận",
      dataIndex: "id",
      key: "id"
    },
    {
      title: "Thời gian tạo",
      key: "created_time",
      render: (text, record) => (
        <span>{moment(record.created_time).format("LLLL")}</span>
      )
    },
    {
      title: "Thông tin bình luận",
      key: "message",
      dataIndex: "message"
    }
  ];

  const { Title } = Typography;
  return (
    <div>
      <h1 style={{ padding: 10, margin: 10 }}>Quản lý bài viết {id}</h1>
      <Button
        style={{ margin: 12 }}
        type="primary"
        size="large"
        disabled={toDelete}
        // onClick={handleDelete()}
        onClick={() => handleDelete(deleteRows)}
      >
        Xóa
      </Button>

      <Button
        style={{
          margin: 12,
          backgroundColor: toDelete ? "#f7f7f7" : "green",
          color: toDelete ? "#cccccc" : "white"
        }}
        type="primary"
        size="large"
        disabled={toDelete}
        onClick={handleKeepData}
      >
        Giữ
      </Button>
      <Popover
        title={text}
        content={content}
        trigger="click"
        placement="bottom"
      >
        <Button style={{ margin: 10 }} type="danger" size="large">
          Xóa tự động
        </Button>
      </Popover>
      <Link style={{ fontSize: 20, fontWeight: 400, marginLeft: 10 }} to="/">
        Trở về trang chủ
      </Link>
      <Table
        rowSelection={{
          type: "checkbox",
          ...rowSelection
        }}
        dataSource={data}
        columns={columns}
        rowKey="id"
      />

      <div
        className="status"
        style={{
          border: "1px solid green",
          background: "gainsboro",
          padding: 12
        }}
      >
        <p>Trạng thái</p>
      </div>

      <div class="auto-section">
        <div className="keep-id">
          {keepRows.length > 0 ? (
            <div style={{ margin: 20 }}>
              <Title level={2} type="danger">
                {" "}
                Danh sách các comment giữ lại:
              </Title>
              <Table dataSource={keepRows} columns={columns} rowKey="id" />
            </div>
          ) : (
            <p></p>
          )}
        </div>
      </div>
    </div>
  );
};

export default Tabular;
