import React, { useState } from "react";
import "./App.css";
import FacebookLogin from "react-facebook-login";
import axios from "axios";
import moment from "moment";
import "antd/dist/antd.css";
import "moment/locale/vi";
import Tabular from "./components/Tabular";

moment.locale("vi");
function App() {
  const [facebookName, setFBName] = useState(null);
  const [pageToken, setToken] = useState(null);
  const [posts, setPosts] = useState([]);

  // useEffect(() => {}, []);
  const responseFacebook = response => {
    setFBName(response.name);
    fetchUserFeed(response.accessToken);
  };

  const fetchUserFeed = access => {
    axios
      .get(`https://graph.facebook.com/v6.0/me/accounts?access_token=${access}`)
      .then(response => {
        setFBName(response.data.data[0].name);
        setToken(response.data.data[0].access_token);
        return axios.get(
          `https://graph.facebook.com/v6.0/${response.data.data[0].id}/feed/?access_token=${response.data.data[0].access_token}`
        );
      })
      .then(res => {
        setPosts(res.data.data);
      });
  };

  return (
    <div className="App">
      <h1>Tool xóa comment</h1>
      {facebookName ? (
        <div>
          <h3>Fanpage: {facebookName}</h3>
          <Tabular data={posts} token={pageToken} />
        </div>
      ) : (
        <FacebookLogin
          appId="2623573851104227"
          autoLoad={true}
          fields="name,email,picture,pages_show_list,manage_pages,publish_pages"
          callback={responseFacebook}
        />
      )}
    </div>
  );
}

export default App;

